import * as service from '@/service/user'

// initial state
const state = {
  userInfo: {}
}

const getters = {}

// actions
const actions = {
  Login ({ commit, state }, data) {
    service.login(data).then(response => {
      commit('setUserInfo', response.data)
    })
  }
}

// mutations
const mutations = {
  setUserInfo (state, data) {
    state.userInfo = {
      name: data.name,
      id: data.id,
      role: data.role,
      sys_id: data.sys_id
    }
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



