<template>
    <Layout>
        <layout-header></layout-header>
        <Content>
            <div class="login-container">
                <div class="cn-title">
                    百家云双师互动课堂
                </div>
                <div class="en-title">
                    BAIJIAYUN INTERACTIVE CLASSES LOGIN
                </div>
                <Form ref="loginForm" :model="loginForm" :rules="ruleValidate" label-position="top">
                    <FormItem label="手机号" prop="userName">
                        <Input size="large" v-model="loginForm.userName"></Input>
                    </FormItem>

                    <FormItem label="验证码" prop="captchCode">
                        <Row>
                            <Col span="16">
                                <Input size="large" v-model="loginForm.captchCode"></Input>
                            </Col>
                            <Col span="4" offset="1">
                                <Button @click="sendCode()" v-if="!isSendCode">发送验证码</Button>
                                <Button disabled v-if="isSendCode">{{ count }}s后重新发送</Button>
                            </Col>
                        </Row>
                    </FormItem>
                     <FormItem class="save-item">
                        <Button  @click="login('loginForm')">登录</Button>
                    </FormItem>
                </Form>
            </div>

        </Content>
    </Layout>
</template>

<script>
import LayoutHeader from '../common/LayoutHeader'
import * as validate from '@/common/js/validate'
import * as service from '@/service/user'

export default {
    name: 'Login',
    components: {
          LayoutHeader
    },
    data () {
        return {
            isSendCode: false,
            count: 60,
            captchStr: '发送验证码',
            loginForm: {
                userName: '',
                captchCode: ''
            },
            ruleValidate: {
                userName: [{required: true, message: '手机号不能为空', trigger: 'blur'}, {validator: validate.validateMobile, trigger: 'blur'}],
                captchCode: [{required: true, message: '验证码不能为空', trigger: 'blur'}]
            }
        }
    },
    methods: {
        login (loginForm) {
            let me = this
            if (!me.$refs[loginForm].validate()) {
                return false
            }
            let params = {
                userName: loginForm.userName,
                captchCode: loginForm.captchCode
            }
            me.$store.dispatch('Login', params)
        },
        sendCode () {
            let me = this

            me.$refs.loginForm.validateField('userName', (errorMsg) => {
                if (!errorMsg) {
                    me.isSendCode = true
                    service.sendCode().then(response => {
                        me.count = 60
                        setInterval(() => {
                            me.count = me.count - 1
                            if (me.count === 0) {
                                me.isSendCode = false
                            }
                        }, 1000)
                    })
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>
@import './style.less';
</style>



