import Vue from 'vue'
import Login from './login.vue'
import router from '@/router/login'
import iView from 'iview'
import '@/common/less/theme.less'
import store from '@/store'

Vue.use(iView)

/* eslint-disable no-new */
new Vue({
    el: '#login',
    store,
    router,
    components: { Login },
    template: '<Login/>'
})



